body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f9f9f9;
}

nav {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav h1 {
  margin: 0;
  font-size: 1.5rem;
}

.container {
  padding: 20px;
}

.editor {
  margin: 0 auto;
  max-width: 800px;
}

.editor-layout {
  width: 100%;
  height: 300px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  padding: 10px;
  background-color: #fff;
  font-family: monospace;
}

.editor-fields input,
.editor-fields textarea {
  display: block;
  width: 100%;
  margin-bottom: 10px;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.image-uploader {
  margin-bottom: 20px;
}

button {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #218838;
}
